<template>
  <div id="hello">
    <div v-if="loading" class="progress-bar-container">
      <span style="position: absolute;">ÂõæÁâáÂä†ËΩΩ‰∏≠...</span>
      <div class="progress-bar" :style="{ width: loadProgress + '%' }"></div>
    </div>

    <!-- Ëé∑ÂèñÂÖ®ÈÉ®Êï∞ÁªÑ -->
    <ul v-for="(arr, index) in allItems" :key="index">
      <!-- ‰∏ªÊ†áÈ¢ò -->
      <div class="title"><span>{{ titles[index] }}</span></div>
      <li class="box" v-for="(item, index2) in arr" :key="index2" @click="copyImageOrTextToClipboard(item, $event)">
        <!-- ÂèëÁ•®ÁÇπÂáªÈìæÊé• -->
        <a v-if="item.title == 'ÂºÄÂèëÁ•®'" class="fapiao" href="http://fp.imwayson.com" target="_blank"
          @click.stop="copyImageOrTextToClipboard()">{{ fapiao }}</a>
        <!-- ÂâØÊ†áÈ¢ò -->
        <span>{{ item.title }}</span>
        <!-- Â¶ÇÊûúÂ≠òÂú®ÂõæÁâáÂ∞±Â§çÂà∂ÂõæÁâá -->
        <img v-if="item.img" :src="item.img" @load="handleImageLoadAndError(true)" @error="handleImageLoadAndError(false)"
          style="display:none;" />
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>
    <div style="clear: both;height: 300px"></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      fapiao: ">>> ÁÇπÂáªÂºÄÂèëÁ•® <<<",
      titles: ['Êó•Â∏∏‰ΩøÁî®', 'ËΩØ‰ª∂ÈóÆÈ¢ò', 'iOS & iPad', 'Windows', 'macOS', 'Android'],
      loading: true, // Âä†ËΩΩËøõÂ∫¶Êù°Áä∂ÊÄÅ
      loadProgress: 0, // Â∑≤Âä†ËΩΩÂõæÁâáÁöÑËøõÂ∫¶
      totalImages: 0, // Ë¶ÅÂä†ËΩΩÁöÑÂõæÁâáÊÄªÊï∞
      loadedImages: 0, // Â∑≤ÁªèÂä†ËΩΩÁöÑÂõæÁâáÊï∞
      isCopying: false // ÊòØÂê¶Ê≠£Âú®Â§çÂà∂
    };
  },
  computed: {
    // Ëé∑ÂèñÂÖ®ÈÉ®ËØùÊúØ
    ...mapGetters(['allItems']),
  },
  created() {
    // ÂàùÂßãÂåñ totalImages
    this.totalImages = this.allItems.flat().filter(item => item.img).length;
    // È°µÈù¢Âä†ËΩΩÊó∂Ëá™Âä®ÁºìÂ≠òÂõæÁâá
    this.cacheAllImages();
  },
  mounted() {
    console.log("È°µÈù¢Â∑≤Âä†ËΩΩ...");
  },
  methods: {
    // ÁºìÂ≠òÊâÄÊúâÂõæÁâá
    async cacheAllImages() {
      for (const item of this.allItems.flat()) {
        if (item.img) {
          await this.cacheImage(item.img);
        }
      }
    },
    // ÁºìÂ≠òÂçï‰∏™ÂõæÁâá
    async cacheImage(url) {
      if (!('caches' in window)) {
        console.error('ÂΩìÂâçÊµèËßàÂô®‰∏çÊîØÊåÅ Cache API');
        return null;
      }

      try {
        const cache = await caches.open('image-cache');
        const response = await fetch(url);
        if (response.ok) {
          await cache.put(url, response.clone());
          console.log(`ÂõæÁâáÂ∑≤ÁºìÂ≠ò: ${url}`);
          return response.blob();  // ËøîÂõû blob Êï∞ÊçÆ
        } else {
          throw new Error('ÁΩëÁªúËØ∑Ê±ÇÂ§±Ë¥•');
        }
      } catch (error) {
        console.error('ÁºìÂ≠òÂõæÁâáÂ§±Ë¥•:', error);
        return null;
      }
    },
    // Ëé∑ÂèñÂõæÁâáÁöÑ Blob
    async getImageBlob(url) {
      if (!('caches' in window)) {
        console.error('ÂΩìÂâçÊµèËßàÂô®‰∏çÊîØÊåÅ Cache API');
        return null;
      }

      const cache = await caches.open('image-cache');
      const cachedResponse = await cache.match(url);

      if (cachedResponse) {
        console.log(`‰ªéÁºìÂ≠ò‰∏≠ËØªÂèñÂõæÁâá: ${url}`);
        return cachedResponse.blob();
      }

      console.log(`ÁºìÂ≠ò‰∏≠Ê≤°ÊúâÂõæÁâáÔºåÂºÄÂßã‰∏ãËΩΩÂπ∂ÁºìÂ≠ò: ${url}`);
      return await this.cacheImage(url);  // ËøîÂõû‰ªé‰∏ãËΩΩÂπ∂ÁºìÂ≠òÁöÑÂõæÁâá
    },
    // Â§ÑÁêÜÂõæÁâáÂä†ËΩΩ
    handleImageLoadAndError(bool) {
      if (bool) {
        console.log('ÂõæÁâáÂä†ËΩΩÊàêÂäü')
        this.handleImageFunc()
      } else {
        console.error('ÂõæÁâáÂä†ËΩΩÈîôËØØ');
        this.handleImageFunc()
      }
    },
    handleImageFunc() {
      this.loadedImages++;
      this.loadProgress = (this.loadedImages / this.totalImages) * 100;
      if (this.loadedImages === this.totalImages) {
        this.loading = false;
      }
    },
    // Â§çÂà∂ÊñáÊú¨Âà∞Ââ™Ë¥¥Êùø
    async copyToClipboard(data) {
      try {
        // Ê£ÄÊü•ÊµèËßàÂô®ÊòØÂê¶ÊîØÊåÅ Clipboard API
        if (!navigator.clipboard) {
          console.error('ÂΩìÂâçÊµèËßàÂô®‰∏çÊîØÊåÅ Clipboard API');
          return;
        }
        // ‰ΩøÁî® Clipboard API Â∞ÜÊï∞ÊçÆÂÜôÂÖ•Ââ™Ë¥¥Êùø
        await navigator.clipboard.writeText(data);
        console.log('ÂÜÖÂÆπÂ∑≤ÊàêÂäüÂ§çÂà∂Âà∞Ââ™Ë¥¥Êùø');
      } catch (err) {
        // Â¶ÇÊûúÂèëÁîüÈîôËØØÔºåÊçïËé∑Âπ∂Âú®ÊéßÂà∂Âè∞ËæìÂá∫ÈîôËØØ‰ø°ÊÅØ
        console.error('Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÂ§±Ë¥•Ôºö', err);
      }
    },
    // Â§çÂà∂ÂõæÁâáÊàñÊñáÊú¨Âà∞Ââ™Ë¥¥Êùø
    async copyImageOrTextToClipboard(item, event) {
      // ÊòØÂê¶Âú®Â§çÂà∂‰∏≠?
      if (this.isCopying) {
        return;
      }
      this.isCopying = true;

      try {
        if (item.content === '') {
          if (item.img === '') {
            console.error('Êó†ÂèØÂ§çÂà∂ÂÜÖÂÆπ');
            return;
          }

          const imgBlob = await this.getImageBlob(item.img);
          if (!imgBlob) {
            console.error('Êó†Ê≥ïËé∑ÂèñÂõæÁâá');
            return;
          }

          const clipboardItemInput = new ClipboardItem({ 'image/png': imgBlob });
          await navigator.clipboard.write([clipboardItemInput]);
          console.log('ÂõæÁâáÂ∑≤ÊàêÂäüÂ§çÂà∂Âà∞Ââ™Ë¥¥Êùø');
        } else {
          await this.copyToClipboard(item.content);
        }

        const clickedElement = event.target;
        clickedElement.style.backgroundColor = 'red';
        setTimeout(() => {
          clickedElement.style.backgroundColor = '';
        }, 500);
      } catch (err) {
        console.error('Â§çÂà∂Âà∞Ââ™Ë¥¥ÊùøÂ§±Ë¥•Ôºö', err);
      } finally {
        setTimeout(() => {
          this.isCopying = false;
        }, 500);
      }
    }
  }
}
</script>



